<template>
  <div id="Curve" ref="Curve" style="margin-left: -200px;width: 100vw;">

  </div>
</template>

<script>
import * as THREE from "three";
  import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
  export default{
    data(){
      return {
        camera:null,
        scene:null,
        renderer:null,
        T0:0,
        points:null,
        color:new THREE.Color(),
        controls:null,
        count:500,
        Animation:null
      }
    },
    created() {

    },
    mounted() {
      this.init()
      this.addge()

      this.controls =new OrbitControls(this.camera, this.renderer.domElement);//创建控件对象
      // this.controls.maxPolarAngle = Math.PI * 0.4;
      // this.controls.minDistance = 0;
      // this.controls.maxDistance = 100;
      // this.controls.enableKeys = true;
      // this.controls.autoRotate = true;
    },
    methods:{
      init(){
        // 设置相机参数，注意选择透视投影相机，而非正投影相机
        var width = window.innerWidth;
        var height = window.innerHeight;
        this.camera = new THREE.PerspectiveCamera(60, width / height, 1, 1000);
        // this.camera.position.set(0,200,100);
        //创建场景
        this.scene = new THREE.Scene();

        //创建渲染实例
        this.renderer = new THREE.WebGLRenderer({ antialias: true, });// 抗锯齿

        let container=this.$refs.Curve
        this.renderer.setSize( container.clientWidth, window.innerHeight );
        //将渲染节点推入页面节点数
        container.appendChild( this.renderer.domElement );
      },
      addge(){
        // 三维样条曲线CatmullRomCurve3创建一个曲线路径
        var curve = new THREE.CatmullRomCurve3([
            new THREE.Vector3(0, 0, 0),
            new THREE.Vector3(0, 100, 100),
            new THREE.Vector3(0, 0, 200),
            new THREE.Vector3(120, 0, 300),
            new THREE.Vector3(50, 50, 400),
            new THREE.Vector3(0, 0, 500),
            new THREE.Vector3(0, 0, 550),
            new THREE.Vector3(0, 100, 550),
            new THREE.Vector3(0, 500, 450),
            new THREE.Vector3(0, 600, 350),
            new THREE.Vector3(0, 400, 250),
            new THREE.Vector3(0, 300, 100),
            new THREE.Vector3(0, 130, 50),

        ]);
        // 通过曲线生成一个管道几何体
        var geometry = new THREE.TubeBufferGeometry(curve, 200, 30, 30, true);
        geometry=geometry.toNonIndexed()
        var position=geometry.attributes.position
        var colors=[]
        for(let i=0;i<position.count;i++){
          this.color.setHSL(Math.random() * 0.3 + 260, 0.75, Math.random() * 0.25 + 0.75)
          colors.push( this.color.r, this.color.g, this.color.b );
        }
        geometry.setAttribute('color', new THREE.Float32BufferAttribute( colors, 3 ))

        var Material = new THREE.PointsMaterial( { vertexColors: true } );

        var starField = new THREE.Points( geometry, Material );
        this.scene.add(starField)
        // 从曲线上获得501个顶点，数量根据需要自己设置
        this.points = curve.getPoints(this.count);
        console.log(this.points)
        this.animate()
      },
      onWindowResize() {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize( window.innerWidth, window.innerHeight );
      },
      animate() {
        window.addEventListener( 'resize', this.onWindowResize, false );
        if(this.T0 + 1>this.count){
          this.T0=0
          // this.$router.push({name:'pointerLockControls',params:{type:2}})
        }
        try{
          // 相机位置设置为曲线上第i个顶点
          this.camera.position.set(this.points[this.T0].x, this.points[this.T0].y, this.points[this.T0].z);
          // 相机观察点设置为第i个点的下一个点
          this.camera.lookAt(new THREE.Vector3(this.points[this.T0 + 1].x, this.points[this.T0 + 1].y, this.points[this.T0 + 1].z));
        }catch(e){
          //TODO handle the exception
          console.log(this.T0)
        }

      	//循环执行
      	this.Animation=requestAnimationFrame( this.animate );
        this.T0++
      	this.renderer.render( this.scene, this.camera );
      },
    },
    beforeDestroy() {
      cancelAnimationFrame(this.Animation);
    }
  }
</script>

<style>
</style>



